import { Plugin, Action, Controller } from '@ali/4ever-cangjie';

const createOnAction = (customizedAction?: Plugin['onAction']) => {
  return (
    action: Action,
    controller: Controller,
    next: () => void,
  ) => {
    return customizedAction ? customizedAction(action, controller, next) : next();
  };
};

export default createOnAction;
